body {
  margin: 0;
  box-sizing: border-box;
  padding: 0;
}
.resume-root {
  display: flex;
  flex-direction: column;
}

.light-header {
  background-color: #fff;
  padding: 2%;
  display: flex;
  font-family: Georgia, "Times New Roman", Times, serif;
  font-size: larger;
  width: 100%;
  color: rgba(1, 1, 90, 0.833);
}
.dark-header {
  background-color: #333333;
  padding: 2%;
  display: flex;
  gap: 5%;
  font-family: Georgia, "Times New Roman", Times, serif;
  font-size: larger;
  width: 100%;
  color: whitesmoke;
}

.light-mainResume {
  display: flex;
  width: 100%;
  justify-content: center;
  background-color: aliceblue;
}

.dark-mainResume {
  display: flex;
  width: 100%;
  justify-content: center;
  background-color: #333333;
}

.footerResume {
  width: 100%;

  display: flex;
  justify-content: center;
}
.layout-grid {
  display: grid;
  grid-template-columns: auto auto auto auto;
  gap: 1%;
  padding: 2%;
}
.resume-button {
  font-family: Georgia, "Times New Roman", Times, serif;
  font-style: italic;
  font-size: 1.5rem;
  height: 50px;
  margin-top: 1%;
  margin-bottom: 1%;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease-in-out;
}
.title-header {
  margin-left: 7%;
}
.image-header {
  height: 100px;
  width: 200px;
  border-radius: 4px;
}
